* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: all .3s;
}

body {
  color: #fff;
  background-color: #15141a;
}

::-webkit-scrollbar {
  width: 0;
  background: none;
}

.logo {
  z-index: 100;
}

.nav-main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 4.6875rem;
  padding: 1.875rem 8.75rem;
  display: flex;
  position: relative;
}

.nav-main__logo {
  margin-bottom: .5rem;
}

.nav-main__info {
  letter-spacing: 5px;
  font-family: JetBrainsMono Nerd Font;
  font-weight: lighter;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.text__main-title {
  letter-spacing: 7%;
  font-family: Futura;
  font-size: 1.95rem;
}

.text__headers-wrapper, .text__main-description {
  text-align: center;
  padding-inline: 3rem;
}

.text__headers-wrapper {
  margin-bottom: 4.125rem;
  line-height: 205%;
  position: relative;
}

.text__headers-wrapper:after {
  content: "";
  height: 5px;
  width: 20px;
  background-color: #fffc;
  border-radius: 30px;
  position: absolute;
  bottom: -.5rem;
  left: 50%;
  transform: translateX(-50%);
}

.text__main-description {
  max-width: 1000px;
  letter-spacing: 5.5%;
  font-family: JetbrainsMono Nerd Font;
  font-size: 12px;
  line-height: 198.5%;
}

.special-text {
  color: #1d1d1d;
  background-color: #fff;
  border-radius: 5px;
  padding: .25rem;
  font-size: smaller;
  font-weight: bold;
}

.back-button {
  filter: invert();
  cursor: pointer;
  margin-bottom: 3.4375rem;
}

.quote-card {
  height: auto;
  width: auto;
  -webkit-backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #ffffff1d;
  border-radius: 10px;
  padding: 2.5rem 1.5rem;
}

.quote-card__headers {
  margin-bottom: 1.875rem;
  line-height: 150%;
}

.quote-card__movie-title, .quote-card__quote-author {
  letter-spacing: -6%;
  font-family: JetBrainsMono Nerd Font;
  font-size: 1.95rem;
}

.quote-card__movie-title {
  color: #ee1d52;
  margin-bottom: .6875rem;
}

.quote-card__quote-phrase {
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

.quote-card__quote-phrase--quotation-mark {
  margin-right: 10px;
}

.quote-card__quote-phrase--quote-text {
  font-family: JetBrainsMono Nerd Font;
  font-size: .8125rem;
}

.btn {
  -webkit-backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  color: #fff;
  letter-spacing: .45rem;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #ffffff1d;
  border: none;
  border-radius: 10px;
  outline: none;
  margin-top: 1rem;
  padding: .85rem 3.375rem;
  font-family: JetBrainsMono Nerd Font;
  font-weight: 700;
}

.flex-container {
  width: 100vw;
  scroll-snap-type: x proximity;
  justify-content: space-between;
  align-items: center;
  display: flex;
  overflow-x: scroll;
}

.buttons-container {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
  display: flex;
}

.content-section {
  width: 100vw;
  scroll-snap-align: start;
  flex: none;
}

.content-section__home {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.content-section__quotes-page {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem;
  display: flex;
}

.content-section__quotes-page .quote-card {
  width: 70vw;
  min-height: 30vh;
  align-self: center;
}

@media screen and (min-width: 720px) {
  .buttons-container {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    display: flex;
  }

  .text__main-description {
    text-align: center;
    padding-inline: 3rem;
  }
}

